<template>
  <Transition name="slide-fade">
    <div class="agents-list-container" v-show="show">
      <div class="agents-content">
        <div class="agents-header">
          <div class="agents-header-section-left">
            <h2>AI Agents</h2>
            <button @click="goTo('/create-agent')" class="agent-button agent-button-small">
              <i class="fas fa-plus"></i>
              Create
            </button>
          </div>
          <button class="close-button" @click="closeList">√ó</button>
        </div>
        
        <div v-if="loading" class="agents-list">
          <div class="agent-card">
            <div class="agent-info">
              <div class="loading-avatar"></div>
              <div class="agent-details">
                <div class="loading-text"></div>
                <div class="loading-text-small"></div>
              </div>
            </div>
          </div>
        </div>
        
        <div v-else-if="chatAgents.length === 0" class="find-agent-section">
          <span>
            <i class="fas fa-info-circle"></i>
            No chats yet. Start Your First Chat
          </span>
        </div>
        
        <div v-else class="agents-list">
          <div v-for="agent in chatAgents" :key="agent.agentTag" class="agent-item">
            <div class="agent-info">
              <img :src="prepareAvatarUrl(agent.avatar)" :alt="agent.name" class="agent-avatar" />
              <div class="agent-details">
                <h3>{{ agent.name }}</h3>
                <button @click.stop="goTo('/ai-agents/' + agent.agentTag)" class="agent-link-button">{{ agent.agentTag }}</button>
                <small v-if="agent.agentTag !== 'main_agent'" class="agent-last-message-ago">{{ getLastMessageTime(agent.agentTag) }} ago</small>
              </div>
            </div>
            <div class="agent-actions">
              <button 
                class="use-button" 
                @click="selectAgent(agent)"
                :class="{ 'active': isCurrentAgent(agent) }"
              >
                Chat
              </button>
              <button 
                v-if="agent.agentTag !== 'main_agent'"
                class="delete-button" 
                @click="agentToDelete = agent"
              >
                <i class="fas fa-trash"></i>
              </button>
            </div>

            <!-- –ü–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏–µ —É–¥–∞–ª–µ–Ω–∏—è -->
            <Transition name="fade">
              <div v-if="agentToDelete?.agentTag === agent.agentTag" class="delete-confirmation">
                <div class="delete-confirmation-content">
                  <p>Chat history will be deleted and cannot be restored</p>
                  <div class="delete-confirmation-buttons">
                    <button class="delete-confirm-button" @click="deleteAgent(agent); agentToDelete = null">
                      Delete
                    </button>
                    <button class="delete-cancel-button" @click="agentToDelete = null">
                      Cancel
                    </button>
                  </div>
                </div>
              </div>
            </Transition>
          </div>
        </div>

        <div class="find-agent-section">
          <button @click="goTo('/ai-agents')" class="agent-button">
            <i class="fas fa-rocket"></i>
            Explore AI Agents
          </button>
        </div>
      </div>
    </div>
  </Transition>
</template>

<script setup>
import { ref, onMounted, watch, computed, nextTick } from 'vue';
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'

const router = useRouter()
const store = useStore()

const props = defineProps({
  isLightTheme: {
    type: Boolean,
    default: false
  }
});

const agentToDelete = ref(null)

const goTo = (path) => {
  store.commit('TOGGLE_CHAT', false)
  router.push(path);
}

const emit = defineEmits(['close', 'select-agent']);

const loading = ref(true)
const chatAgents = computed(() => store.getters.chatAgents)

const show = computed(() => store.getters.agentsListOpen)

const closeList = () => {
  store.commit('TOGGLE_AGENTS_LIST', false)
  emit('close')
}

const prepareAvatarUrl = (avatarNum) => {
  return `https://solanabitcat.com/data/ai-agents/bitcat-ai-agent-avatar-${avatarNum}.png`
}

const isCurrentAgent = (agent) => {
  return store.getters.currentAgent?.agentTag === agent.agentTag
}

const getLastMessageTime = (agentTag) => {
  const history = JSON.parse(localStorage.getItem('chatHistory') || '{}')
  const agentHistory = history[agentTag] || []
  const lastMessageTime = agentHistory[agentHistory.length - 1]?.timestamp
  
  if (!lastMessageTime) return ''
  
  const now = Date.now()
  const messageDate = typeof lastMessageTime === 'string' ? 
    new Date(lastMessageTime).getTime() : lastMessageTime
  
  const diff = now - messageDate
  const minutes = Math.floor(diff / 60000)
  
  if (minutes < 60) {
    return `${minutes || 1}m`
  } else if (minutes < 1440) { // less than 24 hours
    return `${Math.floor(minutes / 60)}h`
  } else {
    return `${Math.floor(minutes / 1440)}d`
  }
}

// –û–±–Ω–æ–≤–ª—è–µ–º –≤—Ä–µ–º—è –∫–∞–∂–¥—É—é –º–∏–Ω—É—Ç—É
setInterval(() => {
  nextTick()
}, 60000)

const selectAgent = (agent) => {
  store.commit('SELECT_AGENT', agent)
  emit('select-agent', agent)
  emit('close')
}

const sortAgents = (agents) => {
  return agents.sort((a, b) => {
    if (a.agentTag === 'main_agent') return -1
    if (b.agentTag === 'main_agent') return 1
    return 0
  })
}

const deleteAgent = (agent) => {
  if (agent.agentTag === 'main_agent') return // Prevent deletion of main_agent
  store.commit('DELETE_AGENT', agent.agentTag)
}

onMounted(() => {
  store.commit('LOAD_CHAT_AGENTS')
  loading.value = false
})

// Watch for changes in agentInfo
watch(() => store.getters.agentInfo, () => {
  store.commit('LOAD_CHAT_AGENTS')
}, { deep: true })
</script>

