<template>
  <div class="ai-agent-container" 
       :style="!isAbsolute ? { zIndex: zIndex } : { zIndex: 1000 }"
       :class="{ 
         'light-theme': isLightTheme,
         'is-absolute': isAbsolute,
         'is-fullscreen': isFullScreen
       }">
    <!-- AI Button -->
    <button 
      v-if="withButton"
      class="ai-button" 
      :class="{ 'is-active': isOpen }"
      @click="toggleChat"
    >
      <img 
        :src="prepareAvatarUrl(currentAgent.avatar)"
        alt="AI Assistant" 
        class="ai-button-image"
      />
      <div class="ai-button-glow"></div>
      
      <!-- First Visit Tooltip -->
      <Transition name="tooltip">
        <div v-if="showFirstVisitTooltip" class="first-visit-tooltip do-invert">
          <p>Hello! I am your AI Agent. Let's talk</p>
          <button class="tooltip-close" @click.stop="closeTooltip">√ó</button>
        </div>
      </Transition>
    </button>

    <!-- Chat Window -->
    <div v-if="isOpen && isMobile && withButton" class="chat-window-overlay"></div>

    <Transition name="chat-window">
      <div v-if="isOpen" class="chat-window do-invert" 
           :class="{ 
             'mobile': isMobile && withButton,
             'limited-height': !isAbsolute || isFullScreen
           }"
           :style="!isAbsolute ? { maxHeight: maxHeight } : {}">
        <div class="chat-header">
          <div class="header-left">
            <Transition name="tooltip">
              <div v-if="showAgentTooltip" class="agent-tooltip">
                <p>Your AI chats here. Switch between</p>
                <button class="tooltip-close" @click.stop="closeAgentTooltip">√ó</button>
              </div>
            </Transition>
            <button class="avatar-button pulse-animation" type="button" @click.prevent.stop="toggleAgentsList">
              <img :src="prepareAvatarUrl(currentAgent.avatar)" alt="AI Agent" class="header-avatar" />
            </button>
            <div class="header-title">
              <h3 class="chat-header-title">{{ currentAgent.name }}</h3>
              <button @click="openAgentProfile(currentAgent.agentTag)" class="agent-tag-button">{{ currentAgent.agentTag }}</button>
            </div>
          </div>
          <div class="header-controls">
            <button class="theme-toggle" @click="toggleTheme">
              <i class="fas" :class="isLightTheme ? 'fa-moon' : 'fa-sun'"></i>
            </button>
            <button class="close-button" @click="toggleChat">√ó</button>
          </div>
        </div>
        <div class="chat-messages">
          <div v-if="currentAgent.agentTag === 'main_agent'" class="message agent-message">
            <div class="message-content">
              <div class="typing-animation">
                <p v-if="currentAgent.agentTag === 'main_agent'">Hello! How are you today?üëã I am your BITCAT AI Agent. Let me introduce you to our Universe! üöÄ</p>
                <div class="tech-decoration">
                  <div class="tech-line"></div>
                  <div class="tech-dot"></div>
                </div>
              </div>
            </div>
          </div>
          
          <div v-if="currentPageInfo && currentAgent.agentTag === 'main_agent'" class="message agent-message">
            <div class="message-content">
              <div class="typing-animation">
                <p>About page <small>(are you here)</small><br>{{ currentPageInfo.title }}</p>
                <p @click="handleClick" v-html="currentPageInfo.description"></p>
                <p>Let me know if you have any questions</p>
                <div class="tech-decoration">
                  <div class="tech-line"></div>
                  <div class="tech-dot"></div>
                </div>
              </div>
            </div>
          </div>

          <!-- Add more messages here form agent and user -->
          <div v-for="(msg, index) in currentAgentHistory" :key="index" 
               :class="['message', msg.type === 'user' ? 'user-message' : 'agent-message']">
            <div class="message-content">
              <div :class="{ 'typing-animation': msg.type === 'agent' }">
                <p>{{ msg.content }}</p>
                <div v-if="msg.type === 'agent'" class="tech-decoration">
                  <div class="tech-line"></div>
                  <div class="tech-dot"></div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="chat-input">
          <div class="input-wrapper">
            <input 
              type="text" 
              v-model="message" 
              @keyup.enter="!isSending && sendMessage()"
              placeholder="Message..."
            />
          </div>
          <button @click="sendMessage" class="send-button" :disabled="isSending">
            <i class="fas" :class="isSending ? 'fa-spinner fa-spin' : 'fa-paper-plane'"></i>
          </button>
        </div>

        <Transition name="chat-window">
          <AiAgentsList 
            v-if="isOpen"
            :show="showAgentsList"
            :is-light-theme="isLightTheme"
            @close="store.commit('TOGGLE_AGENTS_LIST', false)"
            @select-agent="selectAgent"
          />
        </Transition>
      </div>
      
    </Transition>
    
  </div>
</template>

<script setup>
import { ref, onMounted, onUnmounted, watch, computed, nextTick } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useStore } from 'vuex'
import AiAgentsList from './AiAgentsList.vue'

// –î–æ–±–∞–≤–ª—è–µ–º –Ω–æ–≤—ã–µ –ø—Ä–æ–ø—Å—ã —Å –¥–µ—Ñ–æ–ª—Ç–Ω—ã–º–∏ –∑–Ω–∞—á–µ–Ω–∏—è–º–∏ –¥–ª—è –æ–±—Ä–∞—Ç–Ω–æ–π —Å–æ–≤–º–µ—Å—Ç–∏–º–æ—Å—Ç–∏
const props = defineProps({
  withButton: {
    type: Boolean,
    default: true
  },
  isOpenProp: {
    type: Boolean,
    default: false
  },
  isAbsolute: {
    type: Boolean,
    default: true
  },
  isFullScreen: {
    type: Boolean,
    default: false
  },
  maxHeight: {
    type: String,
    default: '600px'
  },
  zIndex: {
    type: Number,
    default: 1000
  }
})

const store = useStore()
const route = useRoute()
const router = useRouter()

// –ò–∑–º–µ–Ω—è–µ–º –ª–æ–≥–∏–∫—É isOpen - —Ç–µ–ø–µ—Ä—å —É—á–∏—Ç—ã–≤–∞–µ–º –ø—Ä–æ–ø isOpenProp
const isOpen = computed(() => {
  if (props.withButton) {
    return store.getters.chatOpen
  } else {
    return props.isOpenProp
  }
})

const message = ref('')
const isMobile = ref(false)
const isLightTheme = ref(false)
const showAgentsList = computed({
  get: () => store.getters.agentsListOpen,
  set: (value) => store.commit('TOGGLE_AGENTS_LIST', value)
})

const currentAgent = computed(() => store.getters.currentAgent)
const showFirstVisitTooltip = ref(false)
const showAgentTooltip = ref(false)

const isSending = ref(false)
const chatHistory = ref({})
const currentAgentHistory = computed(() => {
  const history = chatHistory.value[currentAgent.value.agentTag] || []
  return history.slice(-20)
})

const toggleChat = () => {
  const isOpenValue = !isOpen.value

  if(isMobile.value && isOpenValue) {
    window.document.body.style.overflow = 'hidden'
  } else {
    window.document.body.style.overflow = 'auto'
  }

  store.commit('TOGGLE_CHAT', isOpenValue)

}

const prepareAvatarUrl = (avatarNum) => {
  return `https://solanabitcat.com/data/ai-agents/bitcat-ai-agent-avatar-${avatarNum}.png`
}

const openAgentProfile = (agentTag) => {
  toggleChat()
  router.push(`/ai-agents/${agentTag}`)
}

const updatePageInfo = () => {
  const routeName = route.name
  if (pagesInfo[routeName]) {
    currentPageInfo.value = {
      ...pagesInfo[routeName],
      routeName
    }
  } else {
    pagesInfo.NoDescription.title = routeName
    currentPageInfo.value = {
      ...pagesInfo.NoDescription,
      routeName
    }
  }
}

const handleClick = (event) => {
  const path = event.target.dataset.path;
  if(!path) return
  goTo(path);
}

const goTo = (path) => {
  isOpen.value = false

  setTimeout(() => {
    router.push(path)
  }, 250)
}

const pagesInfo = {
  Universe: {
    title: "BITCAT Universe! üåå",
    description: "This is your gateway to the exciting world of BITCAT. Here you can explore various game modes with <button class='button-link' data-path='/solo'>Solo Game</button>, <button class='button-link' data-path='/hide-seek'>PvP Game</button>, Multiplayer Game(soon) and <button class='button-link' data-path='/leaderboard'>Leaderboard</button>. Game Market with <button class='button-link' data-path='/nft-market'>NFT Marketplace</button>, Assets Exchange(soon). BITCAT Society with <button class='button-link' data-path='/dao'>DAO Voting</button>, MEME Gallery(soon). Check it out and start your adventure! Find out more by clicking on the About BITCAT Universe button."
  },
  SoloGame: {
    title: "Solo Game üéÆ",
    description: "Challenge yourself in this single-player mode! PUMP cat and to get points(Weight). You need it for <button class='button-link' data-path='/hide-seek'>PvP Game</button> battle. Can you make it to the top? üèÜ"
  },
  Leaderboard: {
    title: "Leaderboard üèÜ",
    description: "Check out the top players by Best score, Cat weight and Multiplier win/lose. Can you make it to the top?"
  },
  Roadmap: {
    title: "BITCAT Roadmap üó∫Ô∏è",
    description: "Explore our exciting plans for the future! See what features and updates are coming next."
  },
  MyBITCAT: {
    title: "My BITCAT Profile üò∫",
    description: "Your personal space where you can manage your BITCAT, check stats, and view your achievements."
  },
  MultiplayerGame: {
    title: "Multiplayer Arena ü§∫",
    description: "Challenge other players in real-time battles! Show your skills and climb the ranks."
  },
  'nft-market': {
    title: "NFT Marketplace üè™",
    description: "Browse, buy, and sell unique BITCAT NFTs. Find rare items and special collections! Coming soon."
  },
  Player: {
    title: "Player Profile üë§",
    description: "View detailed information about players, their achievements, rates, and game history."
  },
  Arena: {
    title: "Battle Arena ‚öîÔ∏è",
    description: "Enter the arena and face off against other players in epic battles!"
  },
  HideSeek: {
    title: "Hide & Seek Game(PvP) üïµÔ∏è",
    description: "A thrilling game mode where stealth and luck are key to victory. No more points to play? Let's PUMP your cat on <button class='button-link' data-path='/solo'>Solo Game</button> to get points üèÜ"
  },
  Dao: {
    title: "DAO Voting üèõÔ∏è",
    description: "Participate in the BITCAT community governance. Vote on proposals and shape the future of the BITCAT Universe! Create your own proposals and share your ideas with the community. It's all for free. You just need to have BITCAT tokents on your wallet to vote."
  },
  Changelog: {
    title: "Changelog üìù",
    description: "Stay up to date with the latest updates and improvements to BITCAT. Here we share our progress and features log."
  },
  NoDescription: {
    title: "",
    description: "I think this page is clear without my explanation. ü§ì"
  }
}

const currentPageInfo = ref(null)

const toggleAgentsList = () => {
  store.commit('TOGGLE_AGENTS_LIST')
}

const selectAgent = (agent) => {
  store.commit('SELECT_AGENT', agent)
}

const addMessageToHistory = (content, type, messageId) => {
  const agentTag = currentAgent.value.agentTag
  if (!chatHistory.value[agentTag]) {
    chatHistory.value[agentTag] = []
  }
  
  if (messageId) {
    // Update existing message
    const messageIndex = chatHistory.value[agentTag].findIndex(msg => msg.id === messageId)
    if (messageIndex !== -1) {
      if(chatHistory.value[agentTag][messageIndex].content === '...') {
        chatHistory.value[agentTag][messageIndex].content = ''
      }

      if (!chatHistory.value[agentTag][messageIndex].content.endsWith(content)) {
          chatHistory.value[agentTag][messageIndex].content += content;
      }
    }
  } else {
    // Add new message
    messageId = Math.random().toString(36).substr(2, 9)
    chatHistory.value[agentTag].push({
      id: messageId,
      content,
      type,
      timestamp: Date.now()
    })
  }

  // Keep only last 20 messages
  if (chatHistory.value[agentTag].length > 20) {
    chatHistory.value[agentTag] = chatHistory.value[agentTag].slice(-20)
  }

  // Save to localStorage
  localStorage.setItem('chatHistory', JSON.stringify(chatHistory.value))
  scrollToBottom()

  return messageId
}

const scrollToBottom = () => {
  nextTick(() => {
    const chatMessages = document.querySelector('.chat-messages')
    if (chatMessages) {
      chatMessages.scrollTop = chatMessages.scrollHeight
    }
  })
}

const sendMessage = async () => {
  if (!message.value.trim() || isSending.value) return

  isSending.value = true
  const userMessage = message.value
  message.value = ''

  // Add user message
  addMessageToHistory(userMessage, 'user')
  
  // Add temporary agent message
  const messageId = addMessageToHistory('...', 'agent')
  
  try {
    store.getters.socket.emit('user-reply', {
      messageId,
      message: userMessage,
      agentTag: currentAgent.value.agentTag
    })
  } catch (error) {
    console.error('Error sending message:', error)
    isSending.value = false
  }
}

const initializeSocket = () => {
  store.getters.socket.on('ai-reply', (data) => {
    const { message, agentTag, messageId } = data
    
    if (agentTag === currentAgent.value.agentTag) {
      addMessageToHistory(message, 'agent', messageId)
      // Remove last agent message and add new one
      // const agentMessages = chatHistory.value[agentTag].filter(msg => msg.type === 'agent')
      // if (agentMessages.length > 0) {
      //   const lastIndex = chatHistory.value[agentTag].indexOf(agentMessages[agentMessages.length - 1])
      //   if (lastIndex !== -1) {
      //     chatHistory.value[agentTag][lastIndex].content = message
      //   }
      // }
    }
    
    isSending.value = false
    // scrollToBottom()
  })

  store.getters.socket.on('ai-error', (data) => {
    const { message, agentTag, messageId } = data
    if (agentTag === currentAgent.value.agentTag) {
      addMessageToHistory(message, 'agent', messageId)
      // Remove last agent message and add error message
      // const agentMessages = chatHistory.value[agentTag].filter(msg => msg.type === 'agent')
      // if (agentMessages.length > 0) {
      //   const lastIndex = chatHistory.value[agentTag].indexOf(agentMessages[agentMessages.length - 1])
      //   if (lastIndex !== -1) {
      //     chatHistory.value[agentTag][lastIndex].content = `Error: ${message}`
      //   }
      // }
    }
    isSending.value = false
    // scrollToBottom()
  })
}

const initSocket = async () => {
  await store.dispatch('initSocket')
}

const toggleTheme = () => {
  isLightTheme.value = !isLightTheme.value
}

const checkMobile = () => {
  isMobile.value = window.innerWidth <= 768
}

const checkFirstVisit = () => {
  const hasVisited = localStorage.getItem('bitcat_ai_visited')
  if (!hasVisited) {
    showFirstVisitTooltip.value = true
  }
}

const closeTooltip = () => {
  showFirstVisitTooltip.value = false
  localStorage.setItem('bitcat_ai_visited', 'true')
}

const closeAgentTooltip = () => {
  showAgentTooltip.value = false
  localStorage.setItem('agentTooltipShown', 'true')
}

const checkFirstTimeAgent = () => {
  const tooltipShown = localStorage.getItem('agentTooltipShown')
  if (!tooltipShown) {
    showAgentTooltip.value = true
  }
}

onMounted(async () => {
  if(!props.withButton) {
    await initSocket()
  }
  
  const savedHistory = localStorage.getItem('chatHistory')
  if (savedHistory) {
    chatHistory.value = JSON.parse(savedHistory)
  }
  scrollToBottom()

  checkMobile()
  window.addEventListener('resize', checkMobile)
  checkFirstVisit()
  checkFirstTimeAgent()
})

onUnmounted(() => {
  store.dispatch('cleanup')
})

const socketConnected = computed(() => store.getters.isSocketConnected)
watch(socketConnected, (newValue) => {
  if (newValue) {
    initializeSocket()
  }
})

watch(() => currentAgent.value, () => {
  scrollToBottom()
})

watch(() => isOpen.value, async () => {
  if (isOpen.value) {
    if(!store.getters.isSocketConnected) {
      await initSocket()
    }
  } else {
    // store.dispatch('cleanup')
  }
  scrollToBottom()
})

watch(() => store.getters.currentAgent, (newAgent) => {
  if (newAgent && newAgent.agentTag) {
    // Load chat history for this agent
    const savedHistory = localStorage.getItem('chatHistory')
    if (savedHistory) {
      const parsedHistory = JSON.parse(savedHistory)
      chatHistory.value = parsedHistory
      currentAgentHistory.value = parsedHistory[newAgent.agentTag] || []
    }
    
    // Store agent info if not already stored
    const agentInfo = JSON.parse(localStorage.getItem('agentInfo') || '{}')
    if (!agentInfo[newAgent.agentTag]) {
      agentInfo[newAgent.agentTag] = {
        name: newAgent.name,
        agentTag: newAgent.agentTag,
        avatar: newAgent.avatar
      }
      localStorage.setItem('agentInfo', JSON.stringify(agentInfo))
    }
  }
}, { immediate: true })

watch(() => route.name, (newRoute) => {
  currentPageInfo.value = null
  if (isOpen.value) {
    setTimeout(() => {
      updatePageInfo()
    }, 250)
  }
})

watch(isOpen, (newValue) => {
  if (newValue) {
    updatePageInfo()
  }
})
</script>

